export default {
	data() {
		return {
			lists:[],
			search_title:"约单号",
			clicked:false,
			search:"",
			list_state:""
		};
	},
	onPullDownRefresh(){
		this.refresh();
	},
	onLoad() {
		var me = this;
		me.refresh();
	},
	methods: {
		refresh:function(){
			var me = this;
			uni.showLoading({
				title:"加载中，请稍候"
			})
			uni.request({
				url:this.mainUrl+'order/order/index',
				method:"GET",
				success: (res) => {
					if(res.data.status == "success"){
						var lists = res.data.data;
						me.lists = lists;
						console.log(me.lists);
						for(var i=0;i<me.lists.length;i++){
							if(me.lists[i].status==1){
								me.list_state='进行中'
							}
							if(me.lists[i].status==2){
								me.list_state='人已满'
							}
							if(me.lists[i].status==3){
								me.list_state='已结束'
							}
						}
					}else if(res.data.status=="fail"){
					console.log(res.data.errMsg);
				}
				},
				complete: () => {
					uni.hideLoading();
					uni.stopPullDownRefresh();
				}
			});
		},
		clickHeader:function(){
			var me = this;
			me.clicked=!me.clicked;
			uni.showActionSheet({
				itemList:["约单号","出发时间","发起人","目的地"],
				success: (res) => {
					var itemList = ["约单号","出发时间","发起人","目的地"];
					me.search_title = itemList[res.tapIndex];
					me.clicked=!me.clicked;
				}
			})
		},
	},
	
	computed:{
	},
	
	components: 
	{
		
	}
}

